<template>
  <div class="Head">
  	<div class="log">
  		<img src="../assets/img/log.gif">
  		<img src="../assets/img/text.gif">
  	</div>
  	<div class="exit">
  		<i class="el-icon-message"></i>
  		<i @click="LogOut()" class="el-icon-switch-button"></i>
  	</div>
  </div>
</template>

<script>
export default {
  name: 'Head',
  data(){
  	return {
  		baseUrl: '/api/Video_Forensics',
		param: new FormData(),
  	}
  },
  methods:{
  	LogOut(){
  		var that = this;
  		var url = this.baseUrl + '/logout';
  		this.param.append('token',this.getToken());
  		this.axios.post(url,this.param)
  		.then((res)=>{
  			console.log(res.data);
  			if (res.data.code == 200) {
  				that.$message.success('');
  			}
  		})
  		.catch((e)=>{
  			console.log(e);
  		})
  	},
  	//token
	getToken(){
	var theToken = sessionStorage.getItem('token');
		return theToken.slice(1,theToken.length-1);
	}
  }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.Head {
	width: 100vw;
	height: 57*@allwidth;
	border-bottom: 1px solid #C1E7EB;
	// background: #ECF7D2;
	background: linear-gradient(to bottom right,#fff,#2DCB58);
	display: flex;
	align-items: center;
	.log{
		height: 100%;
		display: flex;
		align-items: center;
		img{
			height: 100%;
		}
	}
	.exit{
		margin-left: auto;
		margin-right: 20px;
		i{
			font-size: 20px;
			padding: 0 3px;
		}
		i:hover{
			color: #58AD1F;
		}
	}
}
</style>
