<template>
	<div class="homework">
		<div class="workhead">
			<i class="el-icon-back" @click="goBack()"></i>
			<!-- <p>{{category}}</p> -->
			<p></p>
		</div>
		<hr>
		<div class="StuList">
	  		<el-row v-for="(list,index) in StuList" :key="index">
			  <el-col :span="18">
			  	<div class="main">
			  		<p>{{list.acount}}</p>
			  		<p>{{list.name}}</p>
			  	</div>
			  </el-col>
			  <el-col :span="4">
			  	<div class="operate">
			  		<p>{{list.grade}}</p>
			  		<i :class="list.isHandwork ? 'el-icon-success':'el-icon-remove'" :title="list.isHandwork?'':''" :style="list.isHandwork?{color: '#98E26E'}:''"></i>
			  		<i class="el-icon-download" @click="showWork(list.id)"></i>
			  	</div>
			  </el-col>
			  <el-col :span="2">
			  		<div class="graded"></div>
			  </el-col>
			</el-row>
	  	</div>
	  	<div class="paging">
	  		<el-pagination
		      @current-change="handleCurrentChange"
		      :current-page.sync="activePage"
		      :page-size="5"
		      :pager-count="5"
		      layout="prev, pager, next"
		      :total="totalPage">
		    </el-pagination>
	  	</div>
	</div>
</template>

<script>
export default {
	name: 'homework',
	data(){
		return {
			baseUrl: '/api/Video_Forensics',
			param: new FormData(),
			activePage: 1,
			totalPage: 1,
			category: '',
			workId: '',
			url: '',
			StuList:[{
	        	id: 1,
	        	acount: '201721209999',
	        	name: 'xiantian',
	        	// isHandwork: false,
	        	isHandwork: true,
	        	isDown: false,
	        	grade: '67'
	        }]
		}
	},
	mounted(){
		this.getListParam();
	},
	methods:{
		getListParam(){
			var courseID = sessionStorage.getItem('courseID');
			this.url = this.baseUrl + '/sSubmit';
			let query = this.$route.query;
			if (query.name == 'test') {
				this.category = '';
			} else {
				this.category = '';
			}
			this.activePage = parseInt(query.page);
			// this.param.append('courseId',courseID);
    		// this.param.append('category',name);
    		this.param.append('hwId',query.id);
    		this.param.append('token',this.getToken());
			this.param.append('pageNum',8);
			this.param.append('currentPage',this.activePage);
			this.getListData();
    	},
    	getListData(){
    		var that = this;
    		this.axios.post(this.url,this.param)
    		.then((res)=>{
    			console.log(res.data);
    		})
    	},
		//
		goBack(){
	       this.$router.go(-1)
		},
		//
		handleCurrentChange(val) {
			console.log(`: ${val}`);
			this.$router.push(`/News/${this.name}/page=${val}`);
			this.param.delete('currentPage');
			this.param.append('currentPage',this.activePage);
		},
		//token
		getToken(){
		var theToken = sessionStorage.getItem('token');
			return theToken.slice(1,theToken.length-1);
		}
	}
}
</script>

<style lang="less">
@allwidth: 100/1386vw;
.homework{
	width: 95%;
	margin: 0 auto;
	hr{
		border: 0.8px solid rgb(61, 126, 141);
	}
	.workhead{
		height: 45*@allwidth;	
		font-size: 22*@allwidth;
		display: flex;
		align-items: flex-end;
		padding-bottom: 10*@allwidth;
		i{
			font-weight: bold;
		}
		p{
			padding-left: 40*@allwidth;
		}
	}
	.StuList{
		padding-top: 5*@allwidth;
		.el-row{
			height: 55*@allwidth;
			border: 1px solid grey;
			border-radius: 5px;
			font-size: 14*@allwidth;
			padding: 0 20*@allwidth;
			margin-bottom: 4*@allwidth;
			color: #999999;
			display: flex;
			align-items: center;
			cursor: pointer;
			.main{
				display: flex;
				align-items: center;
				margin: 5*@allwidth 0;
				p{
					font-size: 18*@allwidth;
					color: #656565;
				}
				p:nth-child(2){
					opacity: 0.7;
					margin-left: 30*@allwidth;
				}
			}
			.graded{
				float: right;
				opacity: 1;
				height: 45*@allwidth;
				line-height: 45*@allwidth;
				text-align: center;
				width: 45*@allwidth;
				border-radius: 50%;
				background: grey;
				color: #fff;
				font-size: 16*@allwidth;
			}
			.operate{
				font-size: 25*@allwidth;
				margin-left: 16*@allwidth;
				display: flex;
				p{
					padding: 0 10*@allwidth;
				}
				i{
					padding: 0 10*@allwidth;
				}
			}
		}
		.el-row:hover{
			border: 2px solid #58CD77;
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}
</style>