<template>
	<div class="">
		<p></p> 
		<el-upload
		  action="/api/aCourse"
		  list-type="picture-card"
		  :on-preview="handlePictureCardPreview"
		  :on-remove="handleRemove">
		  <i class="el-icon-plus"></i>
		</el-upload>
		<el-dialog :visible.sync="dialogVisible">
		  <img width="100%" :src="dialogImageUrl" alt="">
		</el-dialog>
	</div>
</template>

<script>
export default {
	name: '',
	mounted(){
		// this.getPermit()
	},
	data() {
      return {
        dialogImageUrl: '',
        dialogVisible: false
      };
    },
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        console.log(this.dialogImageUrl + '')
        this.dialogVisible = true;
      },
      //
      getNewsList(url){
			var that = this;
			this.axios.post(url,this.param)
	      	.then((response)=>{
	      		var res = response.data;
		        var theJSON = {};
		        that.showList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		title: perData.title,
		        		publisher: perData.publisherId,
		        		time: perData.time
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	console.log(res.data)
		        	that.totalPage = res.data.totalNumber;
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
			        		that.showList.push(formatData(key));
			        	}
		        	} else {
		        		that.$message.warning(that.name + '');
		        	}
		        } else {
		        	that.$message.error(''+ that.name +'');
		        }
		     })
	      	.catch((e)=>{
	      		console.log(e)
		     })
		},
    }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;

</style>